
.options {
    margin-top: 10px;
}
.startBtn {
    margin-top: 20px !important;
    background-color: #aae !important;
}
.startBtn:hover {
    background-color: #aea !important;
}
.block {
    width: 50px;
    float: left;
}
.cols {
    margin-left: 65px;
    margin-right: 60px;
}
.label {
    text-align: center;
}
.rows .rg {
    padding-left: 4px;
}
.cols .rg {
    padding-left: 4px;
}
span.MuiTypography-root-41 {
    margin-top: 2px;
}
.header {
    text-align: center;
    margin-bottom: 20px;
}
.title {
    margin-left: 7px;
    font-size: 16pt;
    line-height: 20px;
    vertical-align: bottom;
    color: #00f;
}
.tile {
    width: 50px;
    height: 50px;
    margin-left: -1px;
    margin-bottom: -1px;
    text-align: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    background-color: #aaa;
    border: 2px solid #fff;
}
.tile:hover {
    width: 52px;
    height: 52px;
    border-width: 1px;
}
.gameBoard {
    border: 2px solid #555;
    padding: 0 0 1px 1px;
}
.row .tile {
    float: left;
}
.row .tile:first-child {
    clear: left;
}



.footer {
    margin-top: 20px;
    text-align: center;
}
.number {
    font-size: 22px;
    font-weight: 700;
    color: #00f;
}
.minesLeft .number {
    color: #f00;
}
.text {
    font-size: 18px;
    color: #555;
    margin-left: 5px;
    line-height: 24px;
    vertical-align: top;
}
.gamePage {
	padding: 30px;
}
.rules {
	font-size: 17pt;
	font-style: italic;
	margin-bottom: 70px;
	text-align: center;
	color: #e5a;
}
.game {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center
}
.controls {
	margin-top: 70px;

	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center
}
.restartBtn {
	background-color: #0080ff !important;
	color: #fff !important;
}
.restartBtn:hover {
	background-color: #00bfff !important;
	color: #000 !important;
}
.newParamsBtn {
	background-color: #ff8000 !important;
	color: #fff !important;

	margin-left: 20px !important;
}
.newParamsBtn:hover {
	background-color: #ffbf00 !important;
	color: #000 !important;
}
body {
	margin: 0;
}
#root {
	background-color: #eee;
	min-height: 100vh;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.content {
	width: 300px;
	padding: 10px;
	box-sizing: border-box;
	background-color: #ccc;
	text-align: center;
}
.emoji {
	text-align: center;
}
