.tile {
    width: 50px;
    height: 50px;
    margin-left: -1px;
    margin-bottom: -1px;
    text-align: center;
    cursor: pointer;
    user-select: none;

    background-color: #aaa;
    border: 2px solid #fff;
}
.tile:hover {
    width: 52px;
    height: 52px;
    border-width: 1px;
}